
import { defineComponent, ref, computed, onMounted } from "vue";
import { useRoute } from "vue-router";
import { constantRoutes } from "../../../router/index";
import { getColum } from '../../../api/index/index'
import { getCssVariableValue } from "../../../utils/index";
import logo from "../logo/index.vue";
import sidebarItem from "./sidebarItem.vue";
import { useStore, mapGetters } from "vuex";

const __default__ = defineComponent({
  computed: {
    ...mapGetters(["getSidebarOpened"]),
  },
  props: {
    isLogo: {
      type: Boolean,
      default: true
    }
  },
  components: {
    logo,
    sidebarItem
  },
  setup() {
    const isCollapse = ref(false);
    const isShowMenu = ref(false);
    const routes = ref([...constantRoutes])
    const store = useStore();
    isCollapse.value = store.state.setting.sidebarOpened
    const route = useRoute();

    const activeMenu = computed(() => {
      const {
        meta: { activeMenu },
        path,
      } = route;
      return activeMenu ? activeMenu : path;
    });

    const v3SidebarMenuBgColor = getCssVariableValue(
      "--v3-sidebar-menu-bg-color"
    );
    const v3SidebarMenuTextColor = getCssVariableValue(
      "--v3-sidebar-menu-text-color"
    );
    const v3SidebarMenuActiveTextColor = getCssVariableValue(
      "--v3-sidebar-menu-active-text-color"
    );

    const backgroundColor = computed(() => v3SidebarMenuBgColor);

    const textColor = computed(() => v3SidebarMenuTextColor);

    const activeTextColor = computed(() => v3SidebarMenuActiveTextColor);

    const tipLineWidth = computed(() => {
      return "2px";
    });

    const sidebarMenuItemHeight = computed(() => {
      return getCssVariableValue("--v3-sidebar-menu-item-height");
    });

    const sidebarMenuHoverBgColor = computed(() => {
      return getCssVariableValue("--v3-sidebar-menu-hover-bg-color");
    });

    const init = async () => {
      let res = await getColum()
      
      if (res.success) {
        routes.value = [...routes.value, ...res.model]
      }
      isShowMenu.value = true
    }


    onMounted(() => {
      init()
    })
    return {
      isShowMenu,
      isCollapse,
      routes,
      activeMenu,
      backgroundColor,
      textColor,
      activeTextColor,
      tipLineWidth,
      sidebarMenuItemHeight,
      sidebarMenuHoverBgColor,
    };
  },
});

import { useCssVars as _useCssVars } from 'vue'
const __injectCSSVars__ = () => {
_useCssVars(_ctx => ({
  "aeaf3406": (_ctx.tipLineWidth),
  "5506d3ef": (_ctx.sidebarMenuItemHeight),
  "c16a4452": (_ctx.sidebarMenuHoverBgColor),
  "ef618c2c": (_ctx.activeTextColor)
}))}
const __setup__ = __default__.setup
__default__.setup = __setup__
  ? (props, ctx) => { __injectCSSVars__();return __setup__(props, ctx) }
  : __injectCSSVars__

export default __default__